import React,{useContext,useEffect,useState} from 'react'   
import '../../../component/exam/jeemain/index.css'
import QuestionJee from '../../../component/exam/jeemain/question';
import ButtonPalateJee from '../../../component/exam/jeemain/buttonPalate';
import { JeemainContext } from '../../../context/jeemainquestions';
import { JE_UPDATE_QUESTION_ALL } from '../../../reducers/jeemain';
import JeTimer from '../../../component/exam/jeemain/timer'
import ErrorPage from '../../../component/exam/common/error'
import Loading from '../../../component/exam/common/loading'
import {firebaseapp} from '../../../firebase/init'
import jsCookie from 'js-cookie'
import { fetchAllQuestionsHs } from '../../../firebase/hs';
export default function ExamPageHS(props){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const [loading,setLoading]=useState(false)
    const [error,setError]=useState("")
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        setLoading(true)
        let res=await fetchAllQuestionsHs()
        if(res.status===200){
            dispatch({type:JE_UPDATE_QUESTION_ALL,payload:res.data})
            let uid=jsCookie.get('uid')
            if(uid)
            await firebaseapp.firestore().collection("users")
            .doc(uid).update({
                examenable:false
            })
        }else{
            setError(res.message)
        }
        setLoading(false)
    }
    if(error){
        return(
            <ErrorPage>{error}</ErrorPage>
        );
    }
    if(loading){
        return(
            <Loading/>
        );
    }
    return(
        <div>
            <JeTimer/>
            <div className="row">
                <div className="col-lg-8 col-md-6 col-sm-12">
                    <QuestionJee/>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-12">
                    <ButtonPalateJee/>
                </div>
            </div>
        </div>
    );
}
