import React,{useContext} from 'react'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import {Card} from 'reactstrap'
import { JeemainContext } from '../../../context/jeemainquestions'
import { JE_UPDATE_ANSWER } from '../../../reducers/jeemain'
export default function OptionsJee(){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const question=jeemaincont.question
    const handleChange=(e)=>{
        dispatch({type:JE_UPDATE_ANSWER,payload:{index:question.index,answer:e.target.value}})
    }
    return(
        <div>
            <Card className="question-options">
                <RadioGroup value={jeemaincont.questions[question.index].answer[0]||""} onChange={handleChange}>
                    <FormControlLabel value="a" control={<Radio/>} label="Option a" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="b" control={<Radio/>} label="Option b" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="c" control={<Radio/>} label="Option c" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="d" control={<Radio/>} label="Option d" style={{margin:10}}></FormControlLabel>
                </RadioGroup>
            </Card>
        </div>
    );
}