import React,{useContext} from 'react'
import './index.css'
import { WbjeeContext } from '../../../context/wbjeequestions';
import {Card,CardText,CardBody,CardHeader,CardFooter} from 'reactstrap'
import WbjeeOptions from './options';
import WbjeeControlButton from './controlButton'
import WbjeeCheckBox from './checkBox';
export default function WbjeeQuestion(){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const question=wbjeecont.question
    return(
        <div>
            <Card>
            <CardHeader className="question-header">
                <p>Catagory: {question.catagory}</p>
                <p>Question no. {question.index+1}</p>
            </CardHeader>
            <CardBody className="question-body">
                {question.text&&<p>{question.text}</p>}
                {question.image&&<a href={question.image} data-lightbox="roadtrip"><img src={question.image}/></a>}
            </CardBody>
            </Card>
            {(question.catagory===3)?<WbjeeCheckBox question={question}/>:<WbjeeOptions question={question}/>}
            <WbjeeControlButton question={question}/>
        </div>
    );
}