import React, { useContext,useEffect,useState } from 'react'
import {Table} from 'reactstrap'
import { WbjeeContext } from '../../../context/wbjeequestions';
import { fetchResult } from '../../../firebase/wbjee';
import { toast } from 'react-toastify';
import {Link} from 'react-router-dom'
import Loading from '../../../component/exam/common/loading';
import ErrorPage from '../../../component/exam/common/error';
import {Button} from '@material-ui/core'
export default function WbjeeResult(){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const [catagory1,setCatagory1]=useState({})
    const [catagory2,setCatagory2]=useState({})
    const [catagory3,setCatagory3]=useState({})
    const [loading,setLoading]=useState(false)
    const [error,setError]=useState("")
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        setLoading(true)
        let answer1=[]
        let answer2=[]
        let answer3=[]
        let i=0
        for(i=0;i<wbjeecont.catagory1.length;i++){
            answer1.push({id:wbjeecont.catagory1[i].id,answer:wbjeecont.catagory1[i].answer})
        }
        for(i=0;i<wbjeecont.catagory2.length;i++){
            answer2.push({id:wbjeecont.catagory2[i].id,answer:wbjeecont.catagory2[i].answer})
        }
        for(i=0;i<wbjeecont.catagory3.length;i++){
            answer3.push({id:wbjeecont.catagory3[i].id,answer:wbjeecont.catagory3[i].answer})
        }
        let res =await fetchResult({answer1,answer2,answer3},wbjeecont.token)
        if(res.status===200){
            setCatagory1(res.data.catagory1)
            setCatagory2(res.data.catagory2)
            setCatagory3(res.data.catagory3)
        }else{
            toast.error(res.message)
            setError(res.message)
        }
        setLoading(false)
    }
    if(loading){
        return(<Loading/>);
    }
    if(error){
        return(
            <ErrorPage/>
        );
    }
    return(
        <div className="wbjee-result-container">
            <h1>Here is your result and you can also find it on to your profile section</h1>
            <div style={{width:"90%",marginTop:20}}>
            <Table striped responsive>
                <thead>
                <tr>
                    <th>#</th>
                    <th>Catagory</th>
                    <th>Right</th>
                    <th>Wrong</th>
                    <th>Total</th>
                    <th>Out of</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>1</td>
                    <td>1</td>
                    <td>{catagory1.pos}</td>
                    <td>{catagory1.neg}</td>
                    <td>{catagory1.total}</td>
                    <td>{catagory1.outof}</td>
                </tr>
                </tbody>
                <tbody>
                <tr>
                    <td>2</td>
                    <td>2</td>
                    <td>{catagory2.pos}</td>
                    <td>{catagory2.neg}</td>
                    <td>{catagory2.total}</td>
                    <td>{catagory2.outof}</td>
                </tr>
                </tbody>
                <tbody>
                <tr>
                    <td>3</td>
                    <td>3</td>
                    <td>{catagory3.pos}</td>
                    <td>0</td>
                    <td>{catagory3.total}</td>
                    <td>{catagory3.outof}</td>
                </tr>
                </tbody>
                <tbody>
                <tr>
                    <td>4</td>
                    <td>Total</td>
                    <td></td>
                    <td></td>
                    <td>{catagory1.total+catagory2.total+catagory3.total}</td>
                    <td>{catagory1.outof+catagory2.outof+catagory3.outof}</td>
                </tr>
                </tbody>
            </Table>
            <div>
                <Button color="primary" variant="contained"
                onClick={()=>{window.open("/wbjee/result/answersheet")}}
                >Display answer sheet</Button>
            </div>
            <div className="wbjee-exam-exit-button">
                <a href="/" style={{textDecoration:"none"}}><Button color="secondary" variant="contained">Back to home</Button></a>
            </div>
            </div>
        </div>
    );
}