export const WB_UPDATE_QUESTION_ALL="wbupdatequestionall"
export const WB_UPDATE_QUESTION="wbupdatequestion"
export const WB_UPDATE_ANSWER="wbupdateanswer"
export const WB_REFRESH_ANSWER="wbrefreshanswer"
let temp=""
export const wbjeeReducer=(state,action)=>{
    switch(action.type){
        case WB_UPDATE_QUESTION_ALL:
            temp={...state}
            temp.catagory1=action.payload.questions1
            temp.catagory2=action.payload.questions2
            temp.catagory3=action.payload.questions3
            temp.question={...action.payload.questions1[0],index:0}
            temp.token=action.payload.token
            return temp
        case WB_UPDATE_QUESTION:
            temp={...state}
            if(action.payload.catagory===1){
                temp.question={...state.catagory1[action.payload.index],index:action.payload.index}
            }else if(action.payload.catagory===2){
                temp.question={...state.catagory2[action.payload.index],index:action.payload.index}
            }else{
                temp.question={...state.catagory3[action.payload.index],index:action.payload.index}
            }
            return temp
        case WB_UPDATE_ANSWER:
            temp={...state}
            if(action.payload.catagory===1){
                temp.catagory1[action.payload.index].answer=[action.payload.answer]
            }else if(action.payload.catagory===2){
                temp.catagory2[action.payload.index].answer=[action.payload.answer]
            }else{
                let val=temp.catagory3[action.payload.index].answer
                if(!val.includes(action.payload.answer))
                temp.catagory3[action.payload.index].answer=[...val,action.payload.answer]
            }
            return temp
        case WB_REFRESH_ANSWER:
            temp={...state}
            if(action.payload.catagory===1){
                temp.catagory1[action.payload.index].answer=[]
            }else if(action.payload.catagory===2){
                temp.catagory2[action.payload.index].answer=[]
            }else{
                temp.catagory3[action.payload.index].answer=[]
            }
            return temp
    }
}