import React, { useContext } from 'react'
import {Card,CardBody,CardText,CardHeader,CardFooter} from 'reactstrap'
import { JeemainContext } from '../../../context/jeemainquestions';
import OptionsJee from './options';
import ControlButtonJee from './controlbutton';
export default function QuestionJee(){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const question=jeemaincont.question
    return(
        <div>
            <Card>
                <CardHeader className="question-header">
                    <p style={{fontSize:25}}>Question No. {question.index+1}</p>
                </CardHeader>
                <CardBody className="question-body">
                    {question.text&&<p>{question.text}</p>}
                    {question.image&&<a href={question.image} data-lightbox="roadtrip"><img src={question.image} /></a>}
                </CardBody>
                <OptionsJee/>
                <ControlButtonJee/>
            </Card>
        </div>
    );
}