import React,{useContext, useEffect,useState} from 'react'
import './introduction.css'
import { AuthContext } from '../../context/auth/AuthContext';
import { fetchUserDetails } from '../../firebase/auth';
import jscookie from 'js-cookie'
import {Checkbox,FormControlLabel,Button} from '@material-ui/core'
import {Table} from 'reactstrap'
import {useHistory} from 'react-router-dom'
const image=require('./assets/profile-default.jpg')

export default function IntroductionPage(){
    const [auth,dispatch]=useContext(AuthContext)
    const [isChecked,setIsChecked]=useState(false)
    const [buttontext,setButtonText]=useState("")
    const [buttonJeText,setButtonJeText]=useState("")
    const [_,setError]=useState("")
    const history=useHistory()
    useEffect(()=>{
        let uid=jscookie.get('uid')
        // console.log(uid)
        fetchUserDetails(uid,dispatch)
    },[])
    useEffect(()=>{
        if(auth.preff==="11joint"){
            setButtonText("Wbjee 11 exam")
            setButtonJeText("Jeemain 11 exam")
        }else if(auth.preff==="12joint"){
            setButtonText("Wbjee 12 exam")
            setButtonJeText("Jeemain 12 exam")
        }else if(auth.preff==="11and12joint"){
            setButtonText("Wbjee 11 and 12 exam")
            setButtonJeText("Jeemain 11 and 12 exam")
        }else if(auth.preff==="11hs"){
            setButtonText("hs exam 11")
        }else if(auth.preff==="12hs"){
            setButtonText("hs exam 12")
        }
        else{
            setButtonText("")
            setButtonJeText("")
        }
    },[auth.preff])

    const handleNext=(str)=>{
        if(isChecked){
            if(auth.email)
            {
            // console.log(str)
            history.push(`/exam/${str}/rules`)
            }
            else
            setError("You have no permission to give the exam")
        }else{
            setError("Please check the checkbox")
        }
    }
    return(
        <div className="container profile-card">
        <div className="row profile">
		<div className="col-md-8">
			<div className="profile-sidebar">
				<div className="profile-userpic">
					<img src={auth.imageurl?auth.imageurl:image} className="img-responsive" alt=""/>
				</div>
				<div className="profile-usertitle">
					<div className="profile-usertitle-name">
						{auth.name?auth.name:"Name"}
					</div>
					<div className="profile-usertitle-job">
						Student of Asim Roy Chowdhury
					</div>
				</div>
                <div className="profile-usermenu">
                <Table hover striped style={{borderColor:"white",color:"white"}}>
                    <thead>
                        <tr>
                        <th>#</th>
                        <th>User Id</th>
                        <th>BatchNo</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                        <th scope="row">1</th>
                        <td>{auth.email?auth.email:"User-id"}</td>
                        <td>{auth.batchno?auth.batchno:"Batch no"}</td>
                        </tr>
                    </tbody>
                    </Table>
                    </div>
                <div className="profile-usermenu">
                <FormControlLabel
                    style={{color:"white"}}
                    control={
                    <Checkbox
                        checked={isChecked}
                        onChange={()=>{
                            setIsChecked(!isChecked)
                        }}
                        name="checkedB"
                        className="checkbox-accept" 
                        style={{color:"white"}}    
                    />
                    }
                    label="I accept all the above information is correct"
                />
            </div>
            <div className="profile-usermenu">
            {buttontext.includes("Wbjee")?
            buttontext==="Wbjee 11 and 12 exam"?
            <div style={{display:"flex",flexDirection:"column"}}>
                <Button 
                onClick={()=>{handleNext("wbjee")}}
                variant="outlined"
                style={{backgroundColor:"white",margin:10}}
                >Wbjee 11 joint</Button>
                <Button
                onClick={()=>{handleNext("wbjee")}}
                variant="outlined"
                style={{backgroundColor:"white",margin:10}}
                >Wbjee 12 joint</Button>
            </div>
            :
            <Button color="primary" 
            variant="outlined" 
            style={{backgroundColor:"white",margin:10}}
            onClick={()=>{handleNext("wbjee")}}>{buttontext}</Button>
            :<div></div>}

            {buttonJeText?
            buttonJeText==="Jeemain 11 and 12 exam"?
            <div style={{display:"flex",flexDirection:"column"}}>
                <Button 
                onClick={()=>{handleNext("jeemain")}}
                variant="outlined"
                style={{backgroundColor:"white",margin:10}}
                >Jeemain 11 joint</Button>
                <Button
                onClick={()=>{handleNext("jeemain")}}
                variant="outlined"
                style={{backgroundColor:"white",margin:10}}
                >Jeemain 12 joint</Button>
            </div>
            :
            <Button color="primary" 
            onClick={()=>{handleNext("jeemain")}}
            variant="outlined" 
            style={{backgroundColor:"white",margin:10}}
            >{buttonJeText}</Button>
            :<div></div>}

            {buttontext.includes("hs") && <Button
            color="primary" 
            onClick={()=>{handleNext("hs")}}
            variant="outlined" 
            style={{backgroundColor:"white",margin:10}}
            >{buttontext}</Button>}
            </div>
			</div>
		</div>
	</div>
</div>
    );
}