import React,{useContext} from 'react'
import {Card,CardBody,Row,Col} from 'reactstrap'
import Button from '@material-ui/core/Button'
import {JeemainContext} from '../../../context/jeemainquestions'
import {toast} from 'react-toastify'
import { JE_UPDATE_SINGLE,JE_REFRESH_ANSWER } from '../../../reducers/jeemain'
export default function ControlButtonJee(){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const question=jeemaincont.question
    const handlePrevious=()=>{
        if(question.index!==0){
            dispatch({type:JE_UPDATE_SINGLE,payload:{index:question.index-1}})
        }else{
            toast.info("You have reached the bigining of the series")
        }
    }
    const handleNext=()=>{
        if(question.index===jeemaincont.questions.length-1){
            toast.info("You have reached end of the series")
            return
        }
        dispatch({type:JE_UPDATE_SINGLE,payload:{index:question.index+1}})
    }
    const handleRefresh=()=>{
        dispatch({type:JE_REFRESH_ANSWER,payload:{index:question.index}})
    }
    return(
        <div>
            <Card>
                <CardBody>
                    <Row>
                    <Col>
                    <Button variant="contained" onClick={handlePrevious}
                    style={{backgroundColor:"#6ab04c"}}>Previous</Button>
                    </Col>
                    <Col>
                    <Button variant="contained" onClick={handleRefresh}
                    style={{backgroundColor:"#1BCA9B"}}>Refresh</Button>
                    </Col>
                    <Col>
                    <Button variant="contained" onClick={handleNext}
                    style={{backgroundColor:"#01CBC6"}}>Next</Button>
                    </Col>
                    </Row>
                </CardBody>
            </Card>
        </div>
    );
}