import Axios from "axios"
import {url} from '../../url'
import jsCookie from 'js-cookie'
export const fetchResultHs=async(answer,jwt)=>{
    try{
        let res=await Axios.post(url+"/hs/result",{jwt,answer})
        if(res.status===200){
            return{
                status:200,
                data:res.data
            }
        }else{
            return{
                status:400,
                message:"Something went wrong"
            }
        }
    }catch(err){
        return{
            status:400,
            message:err.message
        }
    }   
}
export const fetchAllQuestionsHs=async()=>{
    try{
        let jwt=jsCookie.get("userjwt")
        let res=await Axios.get(url+"/hs/fetchAll/"+jwt)
        if(res.status===200){
            return{
                status:200,
                data:res.data
            }
        }else{
            return{
                status:400,
                message:"Something went wrong"
            }
        }
    }catch(Err){
        return{
            status:400,
            message:Err.response.data.message
        }
    }
}