import React,{useEffect,useState,useContext} from 'react'
import { JeemainContext } from '../../../context/jeemainquestions';
import { toast } from 'react-toastify';
import { Table } from 'reactstrap';
import ErrorPage from '../../../component/exam/common/error';
import Loading from '../../../component/exam/common/loading';
import Button from '@material-ui/core/Button'
import { fetchResultHs } from '../../../firebase/hs';
export default function HSResult(){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const [pos,setPos]=useState(0)
    const [neg,setNeg]=useState(0)
    const [outof,setOutof]=useState(0)
    const [error,setError]=useState("")
    const [loading,setLoading]=useState(false)
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        setLoading(true)
        try{
        let questions=jeemaincont.questions
        let answer=[]
        for(let i=0;i<questions.length;i++){
            answer.push({id:questions[i].id,answer:questions[i].answer})
        }
        let res=await fetchResultHs(answer,jeemaincont.token)
        if(res.status===200){
            setPos(res.data.pos)
            setNeg(res.data.neg)
            setOutof(res.data.outof)
        }else{
            toast.error(res.message)
            setError(res.message)
        }
    }catch(err){
        toast.error(err.message)
        setError(err.message)
    }
    setLoading(false)
    }
    if(error){
        return(
        <ErrorPage/>
        )
    }
    if(loading){
        return(
            <Loading/>
        )
    }
    return(
        <div className="jeemain-result-container">
            <h1>Here is your result and you can also find it on to your profile section</h1>
            <div style={{width:"90%",marginTop:20}}>
            <Table striped responsive>  
                <thead>
                    <tr>
                    <th>#</th>
                    <th>Right</th>
                    <th>Wrong</th>
                    <th>Total</th>
                    <th>outof</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1</td>
                        <td>{pos}</td>
                        <td>{neg}</td>
                        <td>{pos+neg}</td>
                        <td>{outof}</td>
                    </tr>
                </tbody>
            </Table>
            </div>
            <div>
                <Button color="primary" variant="contained"
                onClick={()=>{window.open("/hs/result/answersheet")}}
                >Display answer sheet</Button>
            </div>
            <div className="jeemain-exam-exit-button">
                <a href="/" style={{textDecoration:"none"}}><Button color="secondary" variant="contained">Back to home</Button></a>
            </div>
        </div>
    );
}