export const JE_UPDATE_QUESTION_ALL="jeeupdateall"
export const JE_UPDATE_SINGLE="jeeupdatesingle"
export const JE_UPDATE_ANSWER="jeeupdateanswer"
export const JE_REFRESH_ANSWER="jeerefreshanswer"
export const jeemainReducer=(state,action)=>{
    let temp={}
    switch(action.type){
        case JE_UPDATE_QUESTION_ALL:
            temp={...state}
            temp.questions=action.payload.question
            temp.question={...action.payload.question[0],index:0}
            temp.token=action.payload.token
            console.log("From reducer")
            console.log(temp)
            return temp
        case JE_UPDATE_SINGLE:
            temp={...state}
            temp.question={...state.questions[action.payload.index],index:action.payload.index}
            return temp
        case JE_UPDATE_ANSWER:
            temp={...state}
            temp.questions[action.payload.index].answer=[action.payload.answer]
            return temp
        case JE_REFRESH_ANSWER:
            console.log("hiii")
            console.log(action.payload.index)
            temp={...state}
            temp.questions[action.payload.index].answer=[]
            return temp
    }
}