import {url} from '../../url'
import Axios from 'axios'
import jsCookie from 'js-cookie'
export const CatagoryWiseCount=async(excat)=>{
    try{
        const jwt=jsCookie.get('userjwt')
        if(jwt){
            let val=await Axios.get(url+'/wbjee/count/'+jwt)
            if(val.status===200){
                return{
                    status:200,
                    data:val.data.data
                }
            }else{
                return{
                    status:400,
                    message:val.data.message
                }
            }
        }else{
            return {
                status:400,
                message:"Some thing went wrong"
            }
        }
    }catch(err){
        return {
            status:400,
            message:err.message
        }
    }
}

export const fetchAllQuestion=async()=>{
    try{
        let jwt=jsCookie.get("userjwt")
        if(jwt){
            let res=await Axios.get(url+"/wbjee/fetchAll/"+jwt)
            if(res.status===200){
                return{
                    status:200,
                    data:res.data
                }
            }else{
                return{
                    status:res.status,
                    message:res.data.message
                }
            }
        }else{
            return{
                status:400,
                message:"Something wrong happened"
            }
        }
    }catch(err){
        return{
            status:400,
            message:err.message
        }
    }
}

export const fetchResult=async(answer,jwt)=>{
    try{
        let res=await Axios.post(url+"/wbjee/result",{jwt,answer})
        if(res.status===200){
            return{
                status:200,
                data:res.data
            }
        }else{
            return{
                status:res.status,
                message:res.data.message
            }
        }
    }catch(err){
        return{
            status:400,
            message:err.response.data.message
        }
    }
}