import React, { useContext } from 'react'
import { Card, CardBody,Row,Col,CardHeader } from 'reactstrap'
import { WbjeeContext } from '../../../context/wbjeequestions';
import Fab from '@material-ui/core/Fab'
import { WB_UPDATE_QUESTION } from '../../../reducers/wbjee/wbjeeReducer';
export default function WbjeeAnswerPalate(){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const handleChange=(catagory,index)=>{
        dispatch({type:WB_UPDATE_QUESTION,payload:{catagory,index}})
    }
    return(
        <div>
            <Card >
                <CardHeader className="question-options">
                    <p>Catagory 1 questions answer status</p>
                </CardHeader>
                <CardBody>
                    <div style={{width:"100%",display:"flex",justifyContent:"space-between",flexWrap:"wrap"}}>
                    {wbjeecont.catagory1.map((item,index)=>{
                        return(
                            <div key={index}>
                                <Fab style={{backgroundColor:item.answer.length>0?"green":"red",
                                margin:5,color:"white"}} onClick={()=>{handleChange(1,index)}}>
                                    {index+1}
                                </Fab>
                            </div>
                        );
                    })}
                    </div>
                </CardBody>
            </Card>
            <Card>
            <CardHeader className="question-options">
                    <p>Catagory 2 questions answer status</p>
                </CardHeader>
                <CardBody>
                <Row>
                    {wbjeecont.catagory2.map((item,index)=>{
                        return(
                            <Col key={index}>
                                <Fab style={{backgroundColor:item.answer.length>0?"green":"red",
                                margin:5,color:"white"}} onClick={()=>{handleChange(2,index)}}>
                                    {index+1}
                                </Fab>
                            </Col>
                        );
                    })}
                    </Row>
                </CardBody>
            </Card>
            <Card>
            <CardHeader className="question-options">
                    <p>Catagory 3 questions answer status</p>
                </CardHeader>
                <CardBody>
                <Row>
                    {wbjeecont.catagory3.map((item,index)=>{
                        return(
                            <Col key={index}>
                                <Fab style={{backgroundColor:item.answer.length>0?"green":"red",
                                margin:5,color:"white"}} onClick={()=>{handleChange(3,index)}}>
                                    {index+1}
                                </Fab>
                            </Col>
                        );
                    })}
                    </Row>
                </CardBody>
            </Card>
        </div>
    );
}