import React, { useReducer } from 'react'
import { wbjeeReducer } from '../../reducers/wbjee/wbjeeReducer';
export const WbjeeContext=React.createContext()
const quesobj={
    catagory1:[
        {
            id:0,
            image:"",
            text:"",
            answer:[],
            catagory:1
        }
    ],
    catagory2:[
        {
            id:0,
            image:"",
            text:"",
            answer:[],
            catagory:2
        }
    ],
    catagory3:[
        {
            id:0,
            image:"",
            text:"",
            answer:[],
            catagory:3
        }
    ],
    question:{
        id:0,
        image:"",
        text:"",
        answer:[],
        catagory:1,
        index:0
    },
    token:""
}
export default function WbjeeProvider(props){
    const [wbjeecont,dispatch]=useReducer(wbjeeReducer,quesobj)
    return( 
        <WbjeeContext.Provider value={[wbjeecont,dispatch]}>
            {props.children}
        </WbjeeContext.Provider>
    );
}