import React, { useReducer } from 'react'
import {jeemainReducer} from '../../reducers/jeemain/index'
export const JeemainContext=React.createContext()
const quesobj={
    questions:[
        {
            id:0,
            image:"",
            text:"",
            answer:[],
        }
    ],
    question:{
        id:0,
        image:"",
        text:"",
        answer:[],
        index:0
    },
    token:""
}
export default function JeemainProvider(props){
    const [jeemaincont,dispatch]=useReducer(jeemainReducer,quesobj)
    return( 
        <JeemainContext.Provider value={[jeemaincont,dispatch]}>
            {props.children}
        </JeemainContext.Provider>
    );
}