import React,{useContext} from 'react'
import {Card,CardBody,Row,Col} from 'reactstrap'
import Button from '@material-ui/core/Button'
import { WbjeeContext } from '../../../context/wbjeequestions'
import {toast} from 'react-toastify'
import { WB_UPDATE_QUESTION,WB_REFRESH_ANSWER } from '../../../reducers/wbjee/wbjeeReducer'
export default function WbjeeControlButton(props){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const {question}=props
    const handlePrevious=()=>{
        if(question.catagory===1 && question.index===0){
            toast.info("You have reached at the bigining of question series")
        }else if(question.catagory===2 && question.index===0){
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:1,index:wbjeecont.catagory1.length-1}})
        }else if(question.catagory===3 && question.index===0){
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:2,index:wbjeecont.catagory2.length-1}})
        }else{
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:question.catagory,index:question.index-1}})
        }
    }
    const handleRefresh=()=>{
        dispatch({type:WB_REFRESH_ANSWER,payload:{catagory:question.catagory,index:question.index}})
    }
    const handleNext=()=>{
        if(question.catagory===1 && question.index===wbjeecont.catagory1.length-1){
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:2,index:0}})
        }else if(question.catagory===2 && question.index===wbjeecont.catagory2.length-1){
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:3,index:0}})
        }else if(question.catagory===3 && question.index===wbjeecont.catagory3.length-1){
            toast.info("You have reached the last of thequestion series")
        }else{
            dispatch({type:WB_UPDATE_QUESTION,payload:{catagory:question.catagory,index:question.index+1}})
        }
    }
    return(
        <div>
            <Card>
                <CardBody>
                    <Row>
                    <Col>
                    <Button variant="contained" onClick={handlePrevious}
                    style={{backgroundColor:"#6ab04c"}}>Previous</Button>
                    </Col>
                    <Col>
                    <Button variant="contained" onClick={handleRefresh}
                    style={{backgroundColor:"#1BCA9B"}}>Refresh</Button>
                    </Col>
                    <Col>
                    <Button variant="contained" onClick={handleNext}
                    style={{backgroundColor:"#01CBC6"}}>Next</Button>
                    </Col>
                    </Row>
                </CardBody>
            </Card>
        </div>
    );
}