import React,{useEffect, useContext, useState} from 'react'

import { WbjeeContext } from '../../../context/wbjeequestions';
import WbjeeQuestion from '../../../component/exam/wbjee/question'
import WbjeeButtonPalate from '../../../component/exam/wbjee/answerpalate'
import WbjeeTimer from '../../../component/exam/wbjee/timer'
import { WB_UPDATE_QUESTION_ALL } from '../../../reducers/wbjee/wbjeeReducer';
import { fetchAllQuestion } from '../../../firebase/wbjee';
import { toast } from 'react-toastify';
import ErrorPage from '../../../component/exam/common/error';
import Loading from '../../../component/exam/common/loading';
import {firebaseapp} from '../../../firebase/init'
import jsCookie from 'js-cookie'
export default function ExamPageWbjee(){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const [loading,setLoading]=useState(false)
    const [error,setError]=useState("")
    
    useEffect(()=>{
        fetch()
    },[])
    const fetch=async()=>{
        setLoading(true)
        let res=await fetchAllQuestion()
        if(res.status===200){
            dispatch({type:WB_UPDATE_QUESTION_ALL,payload:res.data})
            let uid=jsCookie.get('uid')
            if(uid)
            await firebaseapp.firestore().collection("users").doc(uid).update({
                examenable:false
            })
        }else{
            toast.error(res.message)
            setError(res.message)
        }
        setLoading(false)
    }
    if(loading){
        return(
            <Loading/>
            )
    }
    if(error.length>0){
        return(
            <ErrorPage>{error}</ErrorPage>
        );
    }
    return(
        <React.Fragment>
            <WbjeeTimer/>
            <div className="row">
            <div className="col-lg-8 col-md-8 col-sm-12">
                <WbjeeQuestion/>
            </div>
            <div className="col-lg-4 col-md-4 col-sm-12">
                <WbjeeButtonPalate/>
            </div>
            </div>
        </React.Fragment>
    );
}