import React from 'react';
import {Button} from 'reactstrap';
import {withRouter} from 'react-router-dom'
import {firebaseapp} from '../../../firebase/init'
import jsCookie from 'js-cookie'
import {Row} from 'reactstrap'
class Timer extends React.Component{

    constructor(props)
    {
      super(props);
      this.state={
        clock:59,
        minute:59,
        hour:1,
        portrait:true,
        landscape:false
      };
      this.myfunc=this.myfunc.bind(this);
      this.myfunc1=this.myfunc1.bind(this);
      this.myfunc2=this.myfunc2.bind(this)
      this.finalSubmit=this.finalSubmit.bind(this)
    }
        async componentWillUnmount(){
            clearInterval(this.timer)
            clearInterval(this.minuteTimer)
            clearInterval(this.hourclock)
            clearInterval(this.finalSubmitTimeout)
        }
        componentDidMount(){
            this.timer=setInterval(this.myfunc,1000);
            this.minuteTimer=setInterval(this.myfunc1,60*1000)
            this.hourclock=setInterval(this.myfunc2,3600*1000)
            this.finalSubmitTimeout=setInterval(this.finalSubmit,2*3600*1000+3*1000)
        }
        myfunc()
        {
            const {clock}=this.state
            if(clock<=0)
            {
                this.setState({clock:59})
            }else{
                this.setState({clock:clock-1})
            }
        }
        myfunc1(){
            const {minute}=this.state
            if(minute<=0)
            {
                this.setState({minute:59})
            }else{
                this.setState({minute:minute-1})
            }
        }
        myfunc2(){
            const {hour}=this.state
            if(hour<=0){
            }else{
                this.setState({hour:hour-1})
            }
        }
        finalSubmit(){
            this.props.history.push("/exam/wbjee/result")
        }
    render(){
        return(
            <div style={{padding:20,width:"100%",display:"flex",justifyContent:"center"}}>
            <Row style={{justifyContent:"space-between"}}>
            <Button color="warning"
            style={{color:'#5d4037',fontSize:20,fontWeight:'bold',
            paddingLeft:20,paddingRight:20}}>{this.state.hour}:
            {this.state.minute}:{this.state.clock}</Button>
            <Button color="danger" onClick={this.finalSubmit}>Final submission</Button>
            </Row>
            </div>
        );
    }
}

export default withRouter(Timer)