import {firebaseapp} from '../init'
import { UPDATE_AUTH_ALL } from '../../reducers/auth/authReducer'

export const fetchUserDetails=async(uid,dispatch)=>{
    try{
        let val=await firebaseapp.firestore().collection('users')
        .doc(uid).get()
        if(val.exists){
            // console.log(val.data())
            dispatch({
                type:UPDATE_AUTH_ALL,
                payload:{
                    preff:val.data().grantStatus,
                    uid:uid,
                    batchno:val.data().batchno,
                    email:val.data().email,
                    name:val.data().name,
                    imageurl:val.data().imageurl
                }
            })
        }else{
            dispatch({
                type:"default",
                payload:null
            })
        }
    }catch(err){
        console.log(err)
    }
}
