import React, { useContext } from 'react'
import { Card,CardBody,Row,Col,CardHeader } from 'reactstrap'
import Fab from '@material-ui/core/Fab'
import Tooltip from '@material-ui/core/Tooltip'
import { JeemainContext } from '../../../context/jeemainquestions';
import { JE_UPDATE_SINGLE } from '../../../reducers/jeemain';
export default function ButtonPalateJee(){
    const [jeemaincont,dispatch]=useContext(JeemainContext)
    const handleChange=(index)=>{
        dispatch({type:JE_UPDATE_SINGLE,payload:{index}})
    }
    return(
        <div>
            <Card>
                <CardHeader className="question-header">
                    Buttons for quick access
                </CardHeader>
                <CardBody>
                    <div style={{width:"100%",display:"flex",justifyContent:"space-between",flexWrap:"wrap"}}>
                        {jeemaincont.questions.map((item,index)=>{
                            return(
                            <div key={index}>
                                <Fab style={{backgroundColor:item.answer.length>0?"green":"red",
                                margin:5,color:"white"}} 
                                    onClick={()=>{
                                        handleChange(index)
                                    }}
                                >{index+1}</Fab>
                            </div>)
                        })}
                    </div>
                </CardBody>
            </Card>
        </div>
    );
}