import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import IntroductionPage from './pages/common/introduction';
import RulesWb from './pages/wbjee/rules';
import ExamPageWbjee from './pages/wbjee/exam';
import WbjeeResult from './pages/wbjee/exam/result';
import RulesJe from './pages/jeemain/rules';
import ExamPageJeemain from './pages/jeemain/exam';
import JeResult from './pages/jeemain/exam/result';
import RulesHS from './pages/hs/rules';
import ExamPageHs from './pages/hs/exam'
import HsResult from './pages/hs/exam/result'
function App() {
  return (
    <Router>
      <Switch>
        <Route path="/exam/home" exact component={IntroductionPage}/>
        <Route path="/exam/wbjee/rules" exact component={RulesWb}/>
        <Route path="/exam/wbjee/examination" exact component={ExamPageWbjee}/>
        <Route path="/exam/wbjee/result" exact component={WbjeeResult}/>
        <Route path="/exam/jeemain/rules" exact component={RulesJe}/>
        <Route path="/exam/jeemain/examination" exact component={ExamPageJeemain}/>
        <Route path="/exam/jeemain/result" exact component={JeResult}/>
        <Route path="/exam/hs/rules" exact component={RulesHS}/>
        <Route path="/exam/hs/examination" exact component={ExamPageHs}/>
        <Route path="/exam/hs/result" exact component={HsResult}/>
      </Switch>
    </Router>
  );
}

export default App;
