import React, { useContext } from 'react'
import {Card,CardBody,CardText} from 'reactstrap'
import RadioGroup from '@material-ui/core/RadioGroup'
import Radio from '@material-ui/core/Radio'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import { WbjeeContext } from '../../../context/wbjeequestions'
import { WB_UPDATE_ANSWER } from '../../../reducers/wbjee/wbjeeReducer'
export default function WbjeeOptions(props){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const {question}=props
    const handleChange=(e)=>{
        dispatch({type:WB_UPDATE_ANSWER,
            payload:{index:question.index,catagory:question.catagory,answer:e.target.value}
        })
    }
    const value=()=>{
        if(question.catagory===1){
           return wbjeecont.catagory1[question.index].answer[0]?wbjeecont.catagory1[question.index].answer[0]:""
        }else if(question.catagory===2){
            return wbjeecont.catagory2[question.index].answer[0]?wbjeecont.catagory2[question.index].answer[0]:""
        }
        return ""
    }
    return(
        <div>
            <Card className="question-options">
                <RadioGroup value={value()} onChange={handleChange}>
                    <FormControlLabel value="a" control={<Radio/>} label="Option a" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="b" control={<Radio/>} label="Option b" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="c" control={<Radio/>} label="Option c" style={{margin:10}}></FormControlLabel>
                    <FormControlLabel value="d" control={<Radio/>} label="Option d" style={{margin:10}}></FormControlLabel>
                </RadioGroup>
            </Card>
        </div>
    );
}