import React,{useState, useCallback, useContext} from 'react'
import FormGroup from '@material-ui/core/FormGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'
import { WbjeeContext } from '../../../context/wbjeequestions'
import { WB_UPDATE_ANSWER } from '../../../reducers/wbjee/wbjeeReducer'
import { Card, CardBody } from 'reactstrap'
export default function WbjeeCheckBox(props){
    const [wbjeecont,dispatch]=useContext(WbjeeContext)
    const {question}=props
    const checked=(str)=>{
        let answer=wbjeecont.catagory3[question.index].answer
        if(answer.includes(str)){
            return true
        }
        return false
    }
    const handleChange=(str)=>{
        dispatch({type:WB_UPDATE_ANSWER,payload:{index:question.index,answer:str}})
    }
    return(
        <div>
        <Card className="question-options">
        <CardBody>
        <FormGroup>
          <FormControlLabel
            control={<Checkbox checked={checked("a")} onChange={()=>{
                handleChange("a")
            }}  />}
            label="Option a"
          />
          <FormControlLabel
            control={<Checkbox checked={checked("b")} onChange={()=>{
                handleChange("b")
            }} />}
            label="Option b"
          />
          <FormControlLabel
            control={<Checkbox checked={checked("c")} onChange={()=>{
                handleChange("c")
            }} />}
            label="Option c"
          />
          <FormControlLabel
            control={<Checkbox checked={checked("d")} onChange={()=>{
                handleChange("d")
            }} />}
            label="Option d"
          />
        </FormGroup>
        </CardBody>
        </Card>
        </div>
    );
}