import React,{useContext,useEffect,useState} from 'react'
import { AuthContext } from '../../../context/auth/AuthContext';
import Fab from '@material-ui/core/Fab'
import Checkbox from '@material-ui/core/Checkbox'
import Button from '@material-ui/core/Button'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import {useHistory} from 'react-router-dom'
import './index.css'
import { confirmBox } from '../../../component/exam/common/listner';
export default function RulesJe(){
    const [auth,_]=useContext(AuthContext)
    const [accept,setAccept]=useState(false)
    const history=useHistory()
    const handleProcceed=()=>{
        if(accept){
            history.push("/exam/jeemain/examination")
            // window.open("/exam/jeemain/examination","__jeemainexam"
            // ,`width=${window.innerWidth},height=${window.innerHeight},
            // menubar=no,location=no,resizable=yes,scrollbars=yes,status=no,fullscreen=yes`)
        }else{
            alert("Please accept the checkbox")
        }
    }
    useEffect(()=>{
        return ()=>{window.removeEventListener("popstate",()=>{
            confirmBox()
        })}
    },[])
    if(auth.email||true){
        return(
            <div className="rules-container">
                <h3 style={{textAlign:"center"}}>Rules and regulation for Jeemain examination ({auth.preff})</h3>
                <div style={{marginLeft:5}}>
                    <p>1.You have questions from Jeemain examination</p>
                    <p>2.Each question carry 4 marks</p>
                    <p>3.Each correct answer means 4 marks and for incorrect answer deduct 1 marks</p>
                    <p>4.The time for the entire examination is <strong>1 hours</strong></p>
                    <p>5.  <Fab style={{backgroundColor:"green"}}></Fab> button is indicating the attempted questions</p>
                    <p>6.  <Fab style={{backgroundColor:"red"}}></Fab> button is indicating the non attempted questions</p>
                    <p>7.Exam will be submitted after the timer expiration automatically.</p>
                    <p>8.There will be a next button for jump into the next question</p>
                    <p>9.There will be a previous button for jump into the previous question</p>
                    <p>10.You can also jump in between any questions by tapping the buttons given in the right side of the 
                    window(for desktop view) or below of the window(for mobile view) </p>
                    <p>11.Detailed result will be shown into the next page of examination so wait for 
                    the result it will take sometime to populate</p>
                    <p>12.Also you can find the result on to your profile section in moreinfo</p>
                    <FormControlLabel
                    style={{display:"flex",width:"100%",justifyContent:"center",padding:20}}
                    control={
                    <Checkbox
                        checked={accept}
                        onChange={()=>{
                            setAccept(!accept)
                        }}
                        name="checkedB"
                        className="checkbox-accept"  
                    />
                    }
                    label="I have read all the information and want to furthur procceed to the examination"
                />
                </div>
                <div className="rules-procceed-button">
                <Button color="secondary" onClick={()=>{
                    handleProcceed()
                }} variant="contained"
                >Procceed to examination</Button>
                </div>
            </div>
        );
    }else{
        return(
            <div>
                <h1>You are not authorize to access this page</h1>
            </div>
        );
    }
}