import React,{useContext,useEffect,useState} from 'react'
import { AuthContext } from '../../../context/auth/AuthContext';
import Fab from '@material-ui/core/Fab'
import Checkbox from '@material-ui/core/Checkbox'
import Button from '@material-ui/core/Button'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import {useHistory} from 'react-router-dom'
import './index.css'
export default function RulesWb(){
    const [auth,_]=useContext(AuthContext)
    const [accept,setAccept]=useState(false)
    const history=useHistory()
    const handleProcceed=()=>{
        if(accept){
            history.push("/exam/wbjee/examination")
        }else{
            alert("Please accept the checkbox")
        }
    }
    if(auth.email||true){
        return(
            <div className="rules-container">
                <h3 style={{textAlign:"center"}}>Rules and regulation for wbjee examination ({auth.preff})</h3>
                <div style={{marginLeft:5}}>
                    <p>1.You have questions from wbjee examination</p>
                    <p>2.There are 3 catagories of questions (a)Catagory1 (b)Catagory2 (c)Catagory3</p>
                    <p>3.<strong>Catagory1:</strong> Only one correct option. Marks: 1/question , Negative marks: 0.25/questions</p>
                    <p>4.<strong>Catagory2:</strong> Only one correct option. Marks: 2/question , Negative marks: 0.5/question</p>
                    <p>5.<strong>Catagory3:</strong> Multiple option correct. Marks: 2/question , Negative Marks: no negative</p>
                    <p>6.Total time of examination is <strong>2 hours</strong></p>
                    <p>7.  <Fab style={{backgroundColor:"green"}}></Fab> button is indicating the attempted questions</p>
                    <p>8.  <Fab style={{backgroundColor:"red"}}></Fab> button is indicating the non attempted questions</p>
                    <p>9.Exam will be submitted after the timer expiration automatically.</p>
                    <p>10.There will be a next button for jump into the next question</p>
                    <p>11.There will be a previous button for jump into the previous question</p>
                    <p>12.You can also jump in between any questions by tapping the buttons given in the right side of the 
                    window(for desktop view) or below of the window(for mobile view) </p>
                    <p>13.Detailed result will be shown into the next page of examination so wait for 
                    the result it will take sometime to populate</p>
                    <p>14.Also you can find the result on to your profile section in moreinfo</p>
                    <FormControlLabel
                    style={{display:"flex",width:"100%",justifyContent:"center",padding:20}}
                    control={
                    <Checkbox
                        checked={accept}
                        onChange={()=>{
                            setAccept(!accept)
                        }}
                        name="checkedB"
                        className="checkbox-accept"  
                    />
                    }
                    label="I have read all the information and want to furthur procceed to the examination"
                />
                </div>
                <div className="rules-procceed-button">
                <Button color="secondary" onClick={()=>{
                    handleProcceed()
                }} variant="contained"
                >Procceed to examination</Button>
                </div>
            </div>
        );
    }else{
        return(
            <div>
                <h1>You are not authorize to access this page</h1>
            </div>
        );
    }
}